import {defaults, modal} from './modal'

export default (_text, title, callbackOk, allowClose) => {
    // 表达式声明
    if (typeof title === 'function') {
        callbackOk = title;
        title = undefined;
    }
    // 函数对象式声明
    else if (typeof _text === 'object') {
        let { text = '', title = defaults.modalTitle, allowClose = false, onClick } = _text

        return modal({
            text,
            title,
            buttons: [ {text: defaults.modalButtonOk, bold: true, onClick: onClick} ],
            isClose: allowClose,
            extraClass: 'remove-on-close',
        })
    }

    return modal({
        text: _text || '',
        title: typeof title === 'undefined' ? defaults.modalTitle : title,
        buttons: [ {text: defaults.modalButtonOk, bold: true, onClick: callbackOk} ],
        isClose: allowClose || false,
        extraClass: 'remove-on-close',
    });
};


